import React from 'react';

class YtbDetail extends React.Component {
  render() {
    const {vlists} = this.props;
    const url = "https://www.youtube.com/watch?v="+vlists.id.videoId,
          title = vlists.snippet.title,
          description = vlists.snippet.description;
    return (
      <div className="col-md-9">
        <iframe src={url} />
        <p>{title}</p>
        <p>{description}</p>
      </div>
    )
  }
}

export default YtbDetail;