import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Route, Switch, BrowserRouter as Router } from 'react-router-dom';
import SearchBar from './components/searchBar';
import YtbSearchList from './components/ytbSearchList';
import YtbList from './components/ytbList';
import { ytbApiServiceCall, ytbGetServiceCall } from './services/ytbSearchData';

class SearchYtb extends Component {
  constructor(props) {
    super(props);

    this.state = {
      vlists: [],
      flists: [],
      filterText: '',
      videoId: '',
      nextPageToken: '',
      prevPageToken: '',
      pageToken: ''
    }
  }

  tokenUpdate(token) {
    ytbApiServiceCall(this.state.filterText, token).then((obj) => {
      this.setState({
        'vlists': obj.vlists
      });
    });
    ytbGetServiceCall(token).then((obj) => {
      this.setState({
        'flists': obj.flists
      });
    });
  };

  filterUpdate(value) {
    this.setState({
      filterText: value
    });
    ytbApiServiceCall(this.state.filterText).then((obj) => {
      this.setState({
        'vlists': obj.vlists
      });
    })
    console.log(value);
  };

  componentDidMount() {
    ytbApiServiceCall(this.state.filterText).then((obj) => {
      this.setState({
        'vlists': obj.vlists
      });
    });
    ytbGetServiceCall().then((obj) => {
      this.setState({
        'flists': obj.flists
      });
    });
  }

  render() {
    return (
      <div className="col-sm-9">
        <div className="searchytb-container mt-3 ml-3">
          <h5>Search Youtube API</h5>
          <SearchBar filterVar={this.state.filterText} filterUpdate={this.filterUpdate.bind(this)} />
          <YtbSearchList vlists={this.state.vlists} />
            {/* <div>
              <Router>
                <SearchBar filterVal={this.state.filterText} filterUpdate={this.filterUpdate.bind(this)} onSearch={() => {
                  this.filterUpdate()
							}} />
              </Router>
              <Switch>
              <Route exact path='/' render={() => (
                <YtbSearchList vlists={this.state.vlists} />
              )} />
              </Switch>
            </div> */}

          
          {/* <YtbList flists={this.state.flists} /> */}
        </div>
      </div>
    )
  }
}

export default SearchYtb;